:root {
  --primary-color: #006FB2;
  --secondary-color: #0D0D0D;
  --large: 2em;
  /* primary colors */
  --black: #000000; 
  --cod-gray: #0D0D0D;
  /* secondary colors -  */
  --lt-mine-shaft: #333333;
  --dk-mine-shaft: #222222;
  --white: #FFFFFF;
  /* fonts */
  --primary-font: "Visby DemiBold";
  --line-spacing: 1.5em;
  /* logo clear space: 10% width */
  /* logo on bg img: add black layer with 60% transparency */

}

* {
  box-sizing: border-box;
}

body {
  background: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: var(--white);
  margin: 0 auto;
  max-width: 1024px;
}
.contact-bar {
  background: var(--primary-color);
  text-align: right;
  padding: 0.5em;
}

.link {
  color: var(--white);
  display: inline-block;
  text-decoration: none;
  margin-right: 3em;
  font-weight: bold;
}
.nav-bar {
  align-items: center;
  background: var(--secondary-color);
  color: white;
  display: flex;
  justify-content: space-around;
  height: 6em;
  padding: 0.5em;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 200ms;
  transition: all 200ms;
  z-index: 1;
}
.small-nav-bar {
  height: 4em;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.nav-image {
  height: 100%;
}
.nav-link {
  color: var(--white);
  font-size: var(--large);
  text-decoration: none;
}
.nav-link:hover, .nav-link-active {
  border-bottom: 0.1em solid var(--white);
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--secondary-color);
  z-index: 2;
}
.splash-container {
  background: url(/gemini-group/static/media/banner1.c02fcb0b.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: var(--white);
  padding: 2em 4em;
  -webkit-transition: all 200ms;
  transition: all 200ms;
}
.splash-title, .splash-subtitle {
  background: rgba(0, 0, 0, 0.5);
  display: inline-block;
  padding: 0.1rem 0.5rem;
  z-index: -1;
}
.splash-title {
  font-size: 3em;
  margin-bottom: 0.5em;
  z-index: -1;
}
.splash-subtitle {
  
  font-size: 2em;
  margin-bottom: 1em;
}
.splash-button {
  background: var(--primary-color);
  color: var(--white);
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.5em 0 0;
  padding: 0.5em 1em;
  text-decoration: none;
}
.register {
  padding: 1em;
  text-align: center;
}
.button {
  background: var(--primary-color);
  color: var(--white);
  cursor: pointer;
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.25em;
  padding: 0.5em 1em;
  text-decoration: none;
}
.info {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.info-text-container {
  flex-grow: 1;
  flex-basis: 300px;
  padding: 0 2em 2em;
}
.info-text {
  line-height: 1.75em;
}
.info-video-container{
  background: #000;
  flex-basis: 400px;
  flex-shrink: 0;
  margin: 0 3em;
}
.reverse {
  flex-direction: row-reverse;
}
.hero {
  padding: 2em;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-content{
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  color: white;
  z-index: 1;
}
.hero-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
